.navbar {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    padding: 2rem 8rem;


}

.navbar a {
    text-decoration: none;
    color: inherit;
}

.logo {
    margin-right: 10px;
    width: 25%;
    padding: 0;

    margin: 0;
}

.list {
    list-style: none;
    display: flex;
    gap: 25px;
}

#toggler, .navbar i {
    display: none;
}


@media screen and (max-width:600px) {
    .navbar {
        padding: 2rem 2rem;
    }

    .menu {
        width: 100%;
        max-height: 0;
        overflow: hidden;


    }
    .logo img{
        width: 200%;
    }
    .list {
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }

    .navbar i {
        display: inline-flex;
        align-items: center;
        cursor: pointer;
        padding: 0rem 0rem;
    }

    #toggler:checked~.menu {
        max-height: 100%;

    }
}

.menu-btn {
    background-color: transparent;
    border-radius: 25%;
    border: 1px solid rgba(0, 0, 0, 0.062);
    padding: 50% !important;
    color: rgba(0, 0, 0, 0.062);
}